import React, {useEffect, useContext} from 'react';
import './Gallery.css'
import Carousel from "../carousel/Carousel";
import {Context} from '../../App';
import {images} from '../../data/data';

function Gallery(props) {
    const context = useContext(Context);
    const width = window.innerWidth;
    useEffect(() => {
        context.toggleBlur();
        if (width < 500) {
            context.changeMenuStatus(false);
        }
        return context.toggleBlur
    }, []);

    const handleClick = (id) => {
        const {history, match} = props;
        context.changeGalleryPage(id);
        history.push(match.url + '/' + id);
    };
    // style={{backgroundImage: `url(${img.src})`}} 
    return (
        <div className={'body-wrapper'}>
            <div className={'container'}>
                {images.map((img, id) => {
                    return (
                        <div className='img' onClick={()=>handleClick(id)}>
                            <img className='imgSrc' src={img.src}/>
                            </div>
                    )
                })}
                {/* <Carousel page={context.galleryPage} handleClick={handleClick}/> */}
            </div>
        </div>
    );
}

export default Gallery;